import {
  AbstractViewContribution,
  FrontendApplicationContribution,
  ReactWidget,
} from "@theia/core/lib/browser";
import {
  inject,
  injectable,
  postConstruct,
} from "@theia/core/shared/inversify";
import React from "@theia/core/shared/react";
import TranslationNotesView, {
  TnTSV,
} from "../../../components/TranslationNotes/TranslationNotesView";
import { getDocumentAsScriptureTSV } from "../../../utils/translationNotes";
import { URI } from "@theia/core";
import { FileService } from "@theia/filesystem/lib/browser/file-service";

@injectable()
export class TnViewerWidget extends ReactWidget {
  static readonly ID = "tn-viewer-widget";

  private uri: string | null = null;

  private tnTsv: TnTSV = {};

  constructor() {
    super();
    this.id = TnViewerWidget.ID;
    this.title.label = "Translation Notes";
    this.title.closable = true;
    this.update();
  }

  @inject(FileService)
  protected readonly fs: FileService;

  protected onUpdateRequest(msg: any): void {
    super.onUpdateRequest(msg);
  }

  public setUri(uri: URI) {
    getDocumentAsScriptureTSV("ACT 1:1", uri, this.fs).then((tnTsv) => {
      this.tnTsv = tnTsv;
      this.update();
    });
  }

  render() {
    /* eslint-disable */console.log(...oo_oo(`3201294511_50_4_50_46_4`,"Rendering Translation Notes"));

    /* eslint-disable */console.log(...oo_oo(`3201294511_52_4_52_34_4`,"URI: ", this.uri));

    /* eslint-disable */console.log(...oo_oo(`3201294511_54_4_54_39_4`,"TN TSV: ", this.tnTsv));

    return (
      <TranslationNotesView tnTsv={this.tnTsv} ref={{ verseRef: "GEN 1:1" }} />
    );
  }
}

@injectable()
export class TnViewerWidgetFrontendContribution
  extends AbstractViewContribution<TnViewerWidget>
  implements FrontendApplicationContribution
{
  constructor() {
    super({
      widgetId: TnViewerWidget.ID,
      widgetName: "Resource Widget",
      defaultWidgetOptions: {
        area: "main",
        rank: 200,
      },
    });
  }

  @postConstruct()
  protected init(): void {}

  async initializeLayout() {
    // await this.openView({ reveal: true });
  }
}
