import {
  AbstractViewContribution,
  FrontendApplicationContribution,
  ReactWidget,
} from "@theia/core/lib/browser";
import { injectable, postConstruct } from "@theia/core/shared/inversify";
import React from "@theia/core/shared/react";

@injectable()
export class ViewerWidget extends ReactWidget {
  static readonly ID = "resource-viewer-widget";

  constructor() {
    super();
    this.id = ViewerWidget.ID;
    this.title.label = "Runtime Widget";
    this.title.closable = true;
    this.update();
  }

  protected onUpdateRequest(msg: any): void {
    super.onUpdateRequest(msg);
  }

  render() {
    return <div>Runtime widget</div>;
  }
}

@injectable()
export class ViewerWidgetFrontendContribution
  extends AbstractViewContribution<ViewerWidget>
  implements FrontendApplicationContribution
{
  constructor() {
    super({
      widgetId: ViewerWidget.ID,
      widgetName: "Resource Widget",
      defaultWidgetOptions: {
        area: "main",
        rank: 200,
      },
    });
  }

  @postConstruct()
  protected init(): void {}

  async initializeLayout() {
    // await this.openView({ reveal: true });
  }
}
